<template>
    <swiper :options="scrollOptions" class="scroll" ref='swiper'>
        <div class="scroll-pulldown" v-if="pullDown"><BLoading :text="pullDownText" inline ref="pullDownLoading"></BLoading></div>
        <swiper-slide><slot></slot></swiper-slide>
        <div class="scroll-pullUp" v-if="pullUp"><BLoading :text="pullUpText" inline ref="pullUpLoading"></BLoading></div>
        <div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
    </swiper>
</template>

<script>
    import {swiper, swiperSlide} from 'vue-awesome-swiper';
    import BLoading from 'basecomponents/loading';
    import {PULL_DOWN_HEIGHT, PULL_DOWN_TEXT_INIT, PULL_DOWN_TEXT_START, PULL_DOWN_TEXT_ING, PULL_DOWN_TEXT_END, PULL_UP_HEIGHT, PULL_UP_TEXT_INIT, PULL_UP_TEXT_START, PULL_UP_TEXT_END, PULL_UP_TEXT_ING} from './config';
    export default {
        name: 'BScroll',
        components: {
            swiper,
            swiperSlide,
            BLoading
        },
        props: {
            scrollbar: {
                type: Boolean,
                default: true
            },
            postlistArr: { // HomePostlistindex.vue
                type: [Array, Object]
            },
            pullDown: {
                type: Boolean,
                default: false
            },
            pullUp: {
                type: Boolean,
                default: false
            }
        },
        watch: {
            postlistArr() { // postlistArrscroll
                this.update();
            }
        },
        created() { this.init(); },
        methods: {
            init() {
                this.scrollOptions = {
                    direction: 'vertical',
                    slidesPerView: 'auto', // slide
                    freeMode: true, // 
                    setWrapperSize: true, // swiper-wrapperstyle="height:xxxpx"
                    scrollbar: {
                        el: this.scrollbar ? '.swiper-scrollbar' : null,
                        hide: true // 
                    },
                    on: {
                        sliderMove: this.scroll, // swipersliderMovesliderscroll
                        touchEnd: this.touchEnd, // swipertouchEndswipertouchEnd
                        transitionEnd: this.scrollEnd // swipertransitionEnd()scrollEnd
                    }
                };
                this.pulling = false; // 
                this.pullDownText = PULL_DOWN_TEXT_INIT;
                this.pullUpText = PULL_UP_TEXT_INIT;
            },
            update() { // swiperupdate()swiperref='swiper',swiper,$refsvuedom
                this.$nextTick(() => { this.$refs.swiper && this.$refs.swiper.swiper.update(); }); // $nextTick()Dom
            },
            scroll() { //   
                const swiper = this.$refs.swiper.swiper;
                this.$emit('scroll', swiper.translate, this.$refs.swiper.swiper); // scroll
                if (this.fulling) { return; }
                if (swiper.translate > 0) { //  translateswiper
                    if (!this.pullDown) { return; }
                    if (swiper.translate > PULL_DOWN_HEIGHT) { // loading
                        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_START);
                    } else {
                        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_INIT);
                    }
                } else if (swiper.isEnd) { //  isEndswiper
                    if (!this.pullUp) { return; }
                    const ispullUp = Math.abs(swiper.translate) + swiper.height - parseInt(swiper.$wrapperEl.css('height')) > PULL_UP_HEIGHT;
                    if (ispullUp) {
                        this.$refs.pullUpLoading.setText(PULL_UP_TEXT_START);
                    } else {
                        this.$refs.pullUpLoading.setText(PULL_UP_TEXT_INIT);
                    }
                }
            },
            touchEnd() {
                if (this.pulling) { return; }
                const swiper = this.$refs.swiper.swiper;
                if (swiper.translate > PULL_DOWN_HEIGHT) {
                    if (!this.pullDown) { return; }
                    this.pulling = true; // pulling
                    swiper.allowTouchMove = false; // swiper
                    swiper.setTransition(swiper.params.speed); // swiper swiper.params.speed300ms
                    swiper.setTranslate(PULL_DOWN_HEIGHT); // swiper
                    swiper.params.virtualTranslate = true; // swiperSwiper;paramsswiper
                    this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_ING); // loadding
                    this.$emit('pullDownEnd', this.pullDownEnd); // pullDownEndpullToRefreshSliderpullDownEnd
                } else if (swiper.isEnd) {
                    const totalHeight = parseInt(swiper.$wrapperEl.css('height'));
                    const ispullUp = Math.abs(swiper.translate) + swiper.height - totalHeight > PULL_UP_HEIGHT;
                    if (ispullUp) {
                        if (!this.pullUp) { return; }
                        this.pulling = true;
                        swiper.allowTouchMove = false;
                        swiper.setTransition(swiper.params.speed);
                        swiper.setTranslate(-(totalHeight + PULL_UP_HEIGHT - swiper.height)); // swiper.height
                        swiper.params.virtualTranslate = true;
                        this.$refs.pullUpLoading.setText(PULL_UP_TEXT_ING);
                        this.$emit('pullUpEnd', this.pullUpEnd); // pullUpEndhome
                    }
                }
            },
            pullDownEnd() {
                const swiper = this.$refs.swiper.swiper;
                this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END);
                this.pulling = false; // 
                swiper.params.virtualTranslate = false;
                swiper.allowTouchMove = true;
                swiper.setTransition(swiper.params.speed);
                console.log(swiper.params);
                swiper.setTranslate(0);
                setTimeout(() => { this.$emit('pullDownTransitionEnd'); }, swiper.params.speed);
            },
            pullUpEnd() {
                const swiper = this.$refs.swiper.swiper;
                this.pulling = false;
                this.$refs.pullUpLoading.setText(PULL_UP_TEXT_END);
                swiper.params.virtualTranslate = false;
                swiper.allowTouchMove = true;
            },
            scrollEnd() {
                const swiper = this.$refs.swiper.swiper;
                // scrollEndbacktop
                this.$emit('scrollEnd', swiper.translate, swiper, this.pulling);
            },
            scrollToTop(speed, runCallbacks) {
                this.$refs.swiper && this.$refs.swiper.swiper.slideTo(0, speed, runCallbacks); // swiperslideTospeedrunCallbacks
            }
        }
};
</script>

<style lang="scss" scoped>
    .scroll{
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    .swiper-slide{
        height: auto;
    }
    .scroll-pulldown, .scroll-pullUp{
        position: absolute;
        left: 0;
        width: 100%;
    }
    .scroll-pulldown{
        bottom: 100%;
        height: 80px;
    }
    .scroll-pullUp{
        top: 100%;
        height: 30px;
    }
</style>



